.content {
  padding-left: 400px;
  position: relative;
  min-height: 100vh;
  z-index: 1;
  background-color: #10121B;

}

.info {
  color: white;
  padding: 5em 0;
}


.title h2 {
  font-weight: 700;
  position: relative;
  padding-bottom: 15px;
  text-transform: uppercase;
  font-size: 2.57rem;
  line-height: 3.2857rem;
}

.title h2:before {
  content: "";
  position: absolute;
  left: 0;
  top: auto;
  bottom: 0;
  height: 5px;
  border-radius: 100px;
  width: 100px;
  background: rgba(3,127,255,.3);
}

.title h2::after {
  width: 35px;
  background: #037fff;
  content: "";
  position: absolute;
  left: 0;
  top: auto;
  bottom: 0;
  height: 5px;
  border-radius: 100px;
} 
