@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@400;600;700;900&display=swap);
body {
  margin: 0;
  font-family: 'Source Sans Pro', sans-serif;
}


.content {
  padding-left: 400px;
  position: relative;
  min-height: 100vh;
  z-index: 1;
  background-color: #10121B;

}

.info {
  color: white;
  padding: 5em 0;
}


.title h2 {
  font-weight: 700;
  position: relative;
  padding-bottom: 15px;
  text-transform: uppercase;
  font-size: 2.57rem;
  line-height: 3.2857rem;
}

.title h2:before {
  content: "";
  position: absolute;
  left: 0;
  top: auto;
  bottom: 0;
  height: 5px;
  border-radius: 100px;
  width: 100px;
  background: rgba(3,127,255,.3);
}

.title h2::after {
  width: 35px;
  background: #037fff;
  content: "";
  position: absolute;
  left: 0;
  top: auto;
  bottom: 0;
  height: 5px;
  border-radius: 100px;
} 

.projects-list {
    margin-top: 5em;
    display: flex; 
    flex-wrap: wrap;
    justify-content: space-evenly;
}

.card {
    flex: 0 0 17%;
}

.card-title{
    color: white;
    text-align: center;
}


 .nav {
    position: fixed;
    left: 0;
    top: 0;
    transform: translateX(0);
    height: 100vh;
    width: 300px;
    background: #191d2b;
    border-right: 1px solid #2e344e;
    z-index: 10;
    transition: all .4s ease-out;
}

.rounded-circle{
    border: 7px solid #2e344e;
}

.image-container {
    padding: 40px 0;
    display: block;
    width: 100%;
    text-align: center;
    border-bottom: 1px solid #2e344e;
}

.inner {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    overflow: hidden;
    overflow-y: auto;
}

.header-menu {
    width: 100%;
    padding: 15px 5px;
    margin-bottom: 18em;
}

.header-menu li {
    list-style: none;
    text-align: center;
    display: block;
    margin-bottom: 0.8em;
}


.header-menu li a {
    text-transform: uppercase;
    font-size: 1.3rem;
    letter-spacing: 1px;
    display: block;
    padding: 5px 0;
    color: #a4acc4;
    transition: all .4s ease-out;
    font-weight: 600;
    position: relative;
    overflow: hidden;
    z-index: 1;
}

.header-menu li a:hover {
    text-decoration: none;
    background-color: #153055;
    color: #0B7FFF;
}
.modal-content {
    background-color: #191D2B;
    color: white;
}

.modal-content a {
    text-decoration: none;
}

.repo-info{
    border: 2px solid #2E344E;
    margin-top: 1em;
    padding: 0.5em 1em;
}

.modal-header {
    border-bottom: 1px solid #2E344E;
}

.modal-footer{
    border-top: 1px solid #2E344E;
}
