.home-info {
    color: white;
    padding: 15em 8em;
    text-align: center;
}

.home-info h2{
    font-weight: bold;
    font-size: 3em;
    margin-bottom: 1em;
}

.home-info span {
    color: #037FFF;
}

.home-info p {
    font-size: 1.3em;
    color: #9FA7BE;
}

.cv-download {
    text-decoration: none;
    font-size: 1.3em;
}

.cv-download:hover {
    text-decoration: none;
}

.social-icon {
    margin-bottom: 1em;
}

.social-icon:first-child {
    margin-right: 1em;
}

.social-icon:last-child{
    margin-left: 1em;
}

.facebook-button .social-svg{
    background-color: white;
}

.github-button .social-svg {
    color: white;
 
}

.github-button .social-svg-mask {
    fill: black !important;
}

.github-button .social-svg-background {
    fill: white !important;
}
.contacts-list {
    max-width: 50em;
    margin: 4em auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

}

.contact {
    flex: 0 0 45%;
    margin-top: 1em;
    padding: 1em;
    background-color: #191D2B;
    color: #a4acc4;
}

.contact p {
    font-size: 1.2em;
}

a:hover{
    text-decoration: none;
}
