.contacts-list {
    max-width: 50em;
    margin: 4em auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

}

.contact {
    flex: 0 0 45%;
    margin-top: 1em;
    padding: 1em;
    background-color: #191D2B;
    color: #a4acc4;
}

.contact p {
    font-size: 1.2em;
}

a:hover{
    text-decoration: none;
}